import React from "react";
import "./App.css";
import Link from "./components/Link";
import axios from "axios";
import "bulma/css/bulma.min.css";

export default class App extends React.Component {
  state = {
    data: [],
  };

  componentDidMount() {
    axios.get("https://shrty-odavhad.herokuapp.com/json").then((response) => {
      const data = response.data;
      this.setState({ data });
    });
  }

  render() {
    return (
      <div className="body">
        <p className="title is-2">shrty</p>
        <table className="table is-striped is-fullwidth">
          <thead>
            <tr>
              <th>
                <p className="title is-5">tag</p>
              </th>
              <th>
                <p className="title is-5">link</p>
              </th>
            </tr>
          </thead>
          <tfoot>
            <tr>
              <th>
                <p className="title is-5">tag</p>
              </th>
              <th>
                <p className="title is-5">link</p>
              </th>
            </tr>
          </tfoot>
          <tbody>
            {this.state.data.map((d, i) => (
              <Link key={d.short_tag} data={d} />
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}
