import React from "react";
import App from "./App";
import Error from "./Error";

export default class Router extends React.Component {
  state = {
    url: "",
    child: "",
  };

  componentDidMount() {
    const url = window.location.pathname;
    if (url === "/") {
      const child = <App />;
      this.setState({ child });
    } else {
      const child = <Error />;
      this.setState({ child });
      this.setState({ url });
    }
  }

  render() {
    return (
      <div>
        {this.state.child}
        <span></span>
      </div>
    );
  }
}
